@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  font-family: 'Rubik';
}

.nav{
  font-family: 'Rubik';
  color: #FFFCFC;
  text-align: center;
  padding: 20px;
  font-style: normal;
  font-weight: 800;
  font-size: 22.2857px;
  line-height: 26px;
}

.mainNav{
  background: #282c34;
}

.filterBody{
  margin: 10px 10px;
  display: inline-flex;
  gap: 5px;
 }

.searchInput input{
  width: 260px;
  height: 33px;
  background: #D9D9D9;
  border-radius: 36px;
  border: none;
}

.searchInput input:focus-visible{
  outline: none;
  text-indent: 10px;
}

ul{
  list-style: none;
}

.selectFormat{
  background: #D9D9D9;
  border-radius: 36px;
  font-weight: 500;
  font-size: 15px;
  line-height: 14px;
  padding: 10px;
  /*width: 54px;*/
  /*height: 33px;*/
}


.formatDropdown{
  display: none;
}

.format{
  display: inline-flex;
  flex-direction: row;
  gap: 7px;
  align-items: center;
}
